<template>
    <h3>About US</h3>
    <p>counter = {{ $store.state.counter }}</p>

    <p>{{ counter }}</p>
    <button @click="addClickHandle">同步增加：mutations</button>
    <button @click="asyncAddClickHandle">异步增加：actions</button>
</template>

<script>
// 用到什么，就导入什么
// ...mapMutations ...mapState
import { mapState, mapMutations, mapActions } from 'vuex'
export default {
    name: 'AboutUS',
    // 注入store中的state
    computed: {
       ...mapState(['counter']) // 等价于 this.$store.state.counter
    },
    methods: {
        ...mapMutations(['addCounter']), // 等价于 this.$store.commit('addCounter', 12)
        ...mapActions(['asyncAddCounter']), // 等价于 this.$store.dispatch('asyncAddCounter')
        addClickHandle() {
            // this.$store.commit('addCounter', 12)
            this.addCounter(12)
        },
        asyncAddClickHandle() {
            // dispatch 调用异步方法
            // this.$store.dispatch('asyncAddCounter')
            this.asyncAddCounter()
        }
    }
}
</script>